.demo {
    font-family: "Overpass", sans-serif;
    text-align: left;
    color: var(--neutral-white);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    border-radius: 0 6rem 0 6rem;
    margin: -5rem 0;
    background-image: url(../Assets/images/bg-pattern-circles.svg),
    linear-gradient(150deg, #2D2E40, #3F4164 80%);
    background-position: -60% 95%, center;
    background-repeat: no-repeat;
}

.demo-text {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.demo-text > p {
    font-weight: 300;
    font-size: 1rem;
    letter-spacing: 0.5px;
    line-height: 28px;
    max-width: 69ch;
}

.demo-picture {
    position: relative;
    top: -4rem;
    margin-bottom: -11rem;
}

.demo-text > h2 {
    font-size: 2.5rem;
    letter-spacing: -1.2px;
    font-weight: 600;
}

@media (max-width: 768px) {
    .demo {
        gap: 1rem;
        margin: 20rem 0;
        padding: 3rem 0 6rem 0;
        flex-direction: column;
        text-align: center;
        background-position: 50% 130%, center;
        background-repeat: no-repeat;
    }

    .demo-text {
        margin: -20rem auto 0 auto;
    }
    
    .demo-text > p {
        width: clamp(45ch, 50%, 75ch);
        margin: 0 auto;
    }

    .demo-picture {
        position: relative;
        top: -20rem;
        margin-bottom: 0;
    }
    
    .demo-picture > img {
        width: 90%;
    }
}